:root {
  --flex-items: 4;
  --flex-items-gap: 10px;
  --border-radius: 5px;
  --primary-color: #0c0c0c;
  --secondary-color: #481e14;
  --third-color: #9b3922;
  --third-color-al: rgba(155, 56, 34, .2);
  --fourth-color: #f2613f;
  --light-color: #eee;
}

h1, h2, h3, h4, h5, h6, body, ul, p {
  margin: 0;
}

body {
  padding: 0;
}

ul, ol {
  padding: 0;
  list-style: none;
}

button {
  color: currentColor;
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  line-height: inherit;
  word-spacing: inherit;
  border: none;
}

img {
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

input {
  border: none;
}

.flex-container {
  gap: var(--flex-items-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-items)  - 1) * var(--flex-items-gap) ) / var(--flex-items) );
}

.flex-container-item img {
  width: 100%;
  margin: 0 auto;
  display: block;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background-color: var(--primary-color);
  color: var(--light-color);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 600px) {
  .container {
    max-width: 600px;
  }
}

@media screen and (min-width: 900px) {
  .container {
    max-width: 900px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.logo {
  flex-shrink: 0;
  align-items: flex-end;
  gap: 3px;
  display: flex;
}

.logo:hover {
  color: currentColor;
  text-decoration: none;
}

.logo .desc {
  font-size: .5em;
}

.search-form {
  height: 40px;
  align-items: center;
  display: flex;
}

.search-form-input {
  border-top-left-radius: var(--border-radius);
  border-bottom-left-radius: var(--border-radius);
  min-width: 100px;
  width: 100%;
  max-width: 300px;
  height: 100%;
  background: var(--third-color-al);
  color: #fff;
  border: none;
  padding: 5px 10px;
}

.search-form-input::-ms-input-placeholder {
  color: #fff;
}

.search-form-input::placeholder {
  color: #fff;
}

.search-form-button {
  color: #fff;
  background: var(--secondary-color);
  height: 100%;
  width: 60px;
  border-top-right-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  cursor: pointer;
  display: block;
}

.header {
  padding: 20px 0;
}

.header-container {
  align-items: center;
  display: flex;
}

.header-logo {
  margin-right: 70px;
}

@media screen and (min-width: 768px) {
  .header-logo {
    margin-right: auto;
  }
}

.header-form {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-form {
    display: flex;
  }
}

.hero-header {
  transition: background .3s;
}

.hero {
  text-align: center;
  margin-bottom: 50px;
  padding: 50px 0;
}

@media screen and (min-width: 768px) {
  .hero {
    padding: 100px 0;
  }
}

.hero-title {
  margin-bottom: 25px;
  font-size: 17px;
}

@media screen and (min-width: 768px) {
  .hero-title {
    margin-bottom: 40px;
    font-size: 40px;
  }
}

.hero-text {
  width: 100%;
  max-width: 600px;
  margin: 0 auto 50px;
  font-size: 12px;
}

@media screen and (min-width: 768px) {
  .hero-text {
    margin-bottom: 40px;
    font-size: 16px;
  }
}

.hero-button {
  border-radius: var(--border-radius);
  background-color: var(--third-color);
  width: fit-content;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px;
  transition: background-color .3s;
  display: flex;
}

.hero-button:hover, .hero-button:focus {
  background-color: var(--secondary-color);
  color: #fff;
  text-decoration: none;
}

.images-section-block {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (min-width: 600px) {
  .images-section-block {
    flex-direction: row;
  }
}

.gallery {
  --flex-items: 1;
  max-width: 300px;
  justify-content: center;
  margin: 0 auto;
}

@media screen and (min-width: 600px) {
  .gallery {
    --flex-items: 2;
    max-width: 600px;
    justify-content: flex-start;
  }
}

@media screen and (min-width: 900px) {
  .gallery {
    --flex-items: 3;
    max-width: 900px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery {
    --flex-items: 4;
    max-width: 1200px;
  }
}

.gallery__link {
  color: currentColor;
  text-decoration: none;
}

.gallery__link img {
  height: 170px;
  border-radius: var(--border-radius);
}

@media screen and (min-width: 1300px) {
  .gallery__link img {
    height: 200px;
  }
}

.info {
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  transition-property: box-shadow;
  transition-duration: .3s;
  transition-timing-function: ease;
  display: none;
  box-shadow: 0 0 3px rgba(100, 100, 100, .5);
}

.gallery__link:hover .info {
  box-shadow: 0 0 3px #646464;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: var(--third-color);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

